/* -------------------------------- accordion ------------------------------- */

.accordion-button {
  font-weight: $font-weight-bold;
}

.accordion-item {
  border-bottom: 1px solid var(--#{$prefix}border-color);
  padding: map-get($spacers, 2);
}

.accordion-button-arrow-icon {
  --phoenix-accordion-btn-icon: url('data:image/svg+xml;base64,');
  --phoenix-accordion-btn-active-icon: url('data:image/svg+xml;base64,');
  [data-bs-theme='dark'] & {
    .accordion-button{
      &::after{
        --phoenix-accordion-btn-icon: url('data:image/svg+xml;base64,');
        --phoenix-accordion-btn-active-icon: url('data:image/svg+xml;base64,');
      }
    }
  }
}
